<template>
  <div>
    <p class="text-center m-0">
      <a-space class="flex-wrap">
        <a-button type="primary" class="mb-16px" @click="workSelect">
          选中工作
        </a-button>
        <a-button type="primary" class="mb-16px" @click="workAll">
          全部工作
        </a-button>
      </a-space>
    </p>
    <a-table
      bordered
      :loading="loading"
      :columns="tableColumns"
      :data-source="dataSourceWithKey"
      :scroll="{ x: true }"
      :pagination="false"
      :row-selection="{
        selectedRowKeys: selectedRowKeys,
        onChange: onSelectChange,
      }"
    >
      <template slot="tokenId" slot-scope="_, record">
        <div class="flex-center">
          <span :title="record.tokenId">{{
            displayOptimize(record.tokenId)
          }}</span>
          <a-button
            type="primary"
            size="small"
            :id="`btn-${record.key}`"
            :data-clipboard-text="record.tokenId"
            style="margin-left: 10px"
            @click="copyAddr(record.key)"
          >
            复制
          </a-button>
        </div>
      </template>
      <template slot="workType" slot-scope="_, record">
        <a-tag v-if="record.workType === 1" color="#1890ff">全职</a-tag>
        <a-tag v-else-if="record.workType === 2" color="#faad14">兼职</a-tag>
      </template>
    </a-table>
  </div>
</template>

<script>
import ClipboardJs from "clipboard";
import { genUniqueKey } from "@/utils/index";

export default {
  props: {
    dataSource: {
      type: Array,
      required: true,
    },
    loading: {
      type: Boolean,
      default: true,
    },
    value: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      selectedRowKeys: [],
    };
  },
  computed: {
    tableColumns() {
      return [
        {
          title: "token id",
          width: 120,
          scopedSlots: { customRender: "tokenId" },
        },
        {
          title: "职业",
          dataIndex: "career",
          width: 90,
        },
        {
          title: "等级",
          dataIndex: "level",
          width: 90,
          sorter: (a, b) => a.level - b.level,
          sortDirections: ["ascend", "descend"],
        },
        {
          title: "力量",
          width: 90,
          dataIndex: "strength",
          sorter: (a, b) => a.strength - b.strength,
          sortDirections: ["ascend", "descend"],
        },
        {
          title: "敏捷",
          width: 90,
          dataIndex: "agility",
          sorter: (a, b) => a.agility - b.agility,
          sortDirections: ["ascend", "descend"],
        },
        {
          title: "体质",
          width: 90,
          dataIndex: "physique",
          sorter: (a, b) => a.physique - b.physique,
          sortDirections: ["ascend", "descend"],
        },
        {
          title: "意志",
          width: 90,
          dataIndex: "volition",
          sorter: (a, b) => a.volition - b.volition,
          sortDirections: ["ascend", "descend"],
        },
        {
          title: "智力",
          width: 90,
          dataIndex: "brains",
          sorter: (a, b) => a.brains - b.brains,
          sortDirections: ["ascend", "descend"],
        },
        {
          title: "精神",
          width: 90,
          dataIndex: "charm",
          sorter: (a, b) => a.charm - b.charm,
          sortDirections: ["ascend", "descend"],
        }
      ];
    },
    dataSourceWithKey() {
      return this.dataSource.map((d) => ({
        ...d,
        key: genUniqueKey(),
        gold: d.gold,
      }));
    },
    groupRecords() {
      const fullTimeRecords = [];
      const partTimeRecords = [];
      const gt2kRecords = [];

      for (const item of this.dataSourceWithKey) {
        if (item.workType === 1) {
          fullTimeRecords.push(item);
        } else {
          partTimeRecords.push(item);
        }
        if (item.gold >= 2000) {
          gt2kRecords.push(item);
        }
      }

      return {
        fullTimeRecords,
        partTimeRecords,
        gt2kRecords,
      };
    },
  },
  methods: {
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys;
      console.log("aa", this.selectedRowKeys)
      this.selectedRows = selectedRows;
      console.log("b", this.selectedRows)
      this.$emit("input", selectedRows);
    },
    displayOptimize(careerAddr) {
      const res = /(\w{2}).+(\w{2})/.exec(careerAddr);
      return `${res[1]}....`;
    },
    copyAddr(rowKey) {
      const clipboard = new ClipboardJs(`#btn-${rowKey}`);
      clipboard.on("success", () => {
        this.$message.success("复制成功");
        clipboard.destroy();
      });
    },
    exec2k() {
      this.selectedRowKeys = this.groupRecords.gt2kRecords.map((r) => r.key);
      this.fireWithRecords(this.groupRecords.gt2kRecords);
    },
    execAll() {
      this.selectedRowKeys = this.dataSourceWithKey.map((d) => d.key);
      this.$emit("input", this.dataSourceWithKey);
      this.$emit("harvestAll");
    },
    workSelect() {
      // this.selectedRowKeys = this.groupRecords.gt2kRecords.map((r) => r.key);
      console.log("quit select: ", this.selectedRows)
      this.$emit("input", this.selectedRows);
      this.$emit("workSelect");
    },
    workAll() {
      this.selectedRowKeys = this.dataSourceWithKey.map((d) => d.key);
      this.$emit("input", this.dataSourceWithKey);
      this.$emit("workAll");
    },
    fireWithRecords(records) {
      console.log("records:", records);
      this.$emit("input", records);
      this.$emit("fire");
    },
  },
};
</script>

<style scoped>
.flex-center {
  display: flex;
  align-items: center;
}
</style>
