<template>
  <base-layout>
    <a-row :gutter="[16, 16]">
      <a-col :md="12" :sm="24">
        <a-alert type="info" message="钱包地址" class="h-284px wallet-card">
          <template #description>
            <template v-if="account">
              <span
                  style="cursor: pointer; margin-right: 5px"
                  id="account"
                  :data-clipboard-text="account"
              >
                {{ account }}
              </span>
              <span v-if="copyStatus" style="color: #19be6b">
                <a-icon type="check-circle"/>
                复制成功
              </span>
              <a-tooltip v-else content="点击复制" placement="right">
                <a-icon
                    id="copy-btn"
                    type="copy"
                    :data-clipboard-text="account"
                    size="20"
                    style="color: #1890ff; cursor: pointer"
                />
              </a-tooltip>
            </template>
            <span v-else>暂无地址信息</span>
            <div style="word-break: break-all">
              <p> 卡片数量 {{ cardsCount }} 张</p>
            </div>
          </template>
        </a-alert>
      </a-col>
      <a-col :md="12" :sm="24">
        <a-alert type="warning" message="使用说明">
          <template #description>
            <p>
              1. 本工具完全开源，开源地址
              <strong>
                <a href="https://github.com/0x0baby/0x0baby.github.io" target="_blank">https://github.com/0x0baby/0x0baby.github.io </a>
              </strong>
            </p>
          </template>
        </a-alert>
      </a-col>
    </a-row>
    <br/>
    <a-row>
      <a-col>
        <PropsTable
            :data-source="propsDataSource"
            :loading="loading"
            v-model="selectedTokens"
            @fire="getAward"
            @workAll="workAll"
            @workSelect="workSelect"
        />
      </a-col>
    </a-row>
  </base-layout>
</template>

<script>
import mixins from "@/mixins";
import PropsTable from "@/components/mybaby/propsTable.vue";
import ClipboardJs from "clipboard";

export default {
  name: "Harvest",
  mixins: [mixins],
  components: {
    PropsTable,
  },
  data() {
    return {
      rate: 0.0,
      total: 0,
      tokens: [],
      selectedTokens: [],
      loading: true,
      feeRate: 10,
      baseFee: 0.001,
      propsDataSource: [],
      copyStatus: 0,
      goldPrice: 0,
      work: null,
      partTimeWork: null,
      playContract: null,
      feeContract: null,
      cardsCount: 0,
    };
  },
  computed: {
    formattedTotal() {
      const total = +this.total;
      if (total) {
        return total.toFixed(2);
      }
      return total;
    },
    totalUsd() {
      const total = +this.total;
      if (total) {
        return (total * this.goldPrice).toFixed(2);
      }
      return 0;
    },
  },
  async created() {
    await this.initWeb3Account();
    if (!this.account) {
      return;
    }

    this.initContract();
    await this.getMyBabyList();
  },
  mounted() {
    window.getProperty = this.getProperty;
    const accountBtn = new ClipboardJs("#account");
    const copyBtn = new ClipboardJs("#copy-btn");

    const successCb = () => {
      this.copyStatus = true;
      setTimeout(() => {
        this.copyStatus = false;
      }, 3000);
    };

    copyBtn.on("success", successCb);
    accountBtn.on("success", successCb);
  },
  methods: {
    async getMyBabyList() {
      const data = await this.getAllToken(this.account)
      this.propsDataSource = data;
      this.loading = false;
      this.cardsCount = data.length
    },
    calculateFee(tokens) {
      return `${Math.ceil(tokens.length / this.feeRate) * this.baseFee}`;
    },
    async workAll() {
      console.log("work all")
      const selectedTokens = this.selectedTokens;
      console.log("all ", selectedTokens)
      for (const token of selectedTokens) {
        this.milkStationContract.methods.deposit(token.tokenId).send(
            {'from': this.account}
        )
      }

    },
    async workSelect() {
      console.log("work select")
      const selectedTokens = this.selectedTokens;
      console.log("select ", selectedTokens)
      if (selectedTokens === undefined || !selectedTokens.length ) {
        this.$message.error("至少选择一项后操作");
        return
      }
      for (const token of selectedTokens) {
        this.milkStationContract.methods.deposit(token.tokenId).send(
            {'from': this.account}
        )
      }
    },
    async getAllAward() {
      // 支付费用
      const selectedTokens = this.selectedTokens;
      // await this.harvestAll()
      this.payFee(
          selectedTokens, // 收菜
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            this.harvestAll()
          }
      ).catch(() => {
        this.$message.error("支付失败");
      });
    },
    async getAward() {
      const selectedTokens = this.selectedTokens;
      console.log('select tokens: ', selectedTokens)
      if (!selectedTokens.length) {
        this.$message.error("至少选择一项后操作");
        return;
      }

      // for (const token of selectedTokens) {
      //   this.harvest(token.tokenId)
      // }
      // 支付费用
      this.payFee(
          selectedTokens, // 收菜
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            for (const token of selectedTokens) {
              this.harvest(token.tokenId)
            }
          }
      ).catch(() => {
        this.$message.error("支付失败");
      });
    },
    payFee(tokens, callback) {
      const fee = this.calculateFee(tokens);

      return this.web3.eth.sendTransaction(
          {
            from: this.account,
            to: "0x4d09c2578690c7D4285D616f3eB1F53128148738",
            value: this.web3.utils.toWei(fee),
          },
          callback
      );
    },
  },
};
</script>

<style scoped>
@media (max-width: 768px) {
  .wallet-card {
    height: auto;
  }
}
</style>
